.am-opacity-clear {
  z-index: 10;
  opacity: 0;
}
.am-vertical-center {
  top: 50%;
  transform: translateY(-50%);
}
.am-visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  margin: 0;
  padding: 0;
  width: 0;
  height: 0;
  border: 0;
  opacity: 0;
}
.am-visually-hidden-reset {
  position: static;
  overflow: visible;
  clip: auto;
  margin: 0;
  width: auto;
  height: auto;
  opacity: 1;
}
.am-custom-scrollbar {
  overflow: auto;
  scrollbar-color: #dfdedd #ffffff;
  scrollbar-width: thin;
  scrollbar-face-color: #dfdedd;
  scrollbar-highlight-color: #dfdedd;
  scrollbar-shadow-color: #dfdedd;
  scrollbar-track-color: #ffffff;
}
.am-custom-scrollbar::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.am-custom-scrollbar::-webkit-scrollbar-track {
  background-color: transparent;
  box-shadow: none;
}
.am-custom-scrollbar::-webkit-scrollbar-thumb {
  border-radius: 12px;
  background: #dfdedd;
}
.am-custom-scrollbar > * {
  max-width: none;
}
.am-text-uppercase {
  text-transform: uppercase;
  letter-spacing: 0.06em;
  font-weight: 700;
  font-size: 1.2rem;
}
.am-float-clear {
  clear: both;
}
.am-word-break {
  word-wrap: break-word;
  word-break: break-word;
}
.ie11 .am-word-break {
  word-break: break-all;
}
.am-overflow-fade {
  position: relative;
  padding: 0;
}
.am-overflow-fade:after {
  position: absolute;
  top: 0;
  left: calc(100% - 22px);
  width: 32px;
  height: 100%;
  background: linear-gradient(270deg, #ffffff 0%, rgba(255, 255, 255, 0.65) 44.32%, rgba(255, 255, 255, 0) 92.5%);
  content: '';
  pointer-events: none;
  user-select: none;
}
.no-display {
  display: none;
}
._am-message-icon-inner > *:first-child:before {
  width: 20px;
  text-align: right;
}
.am-product-options dt {
  float: left;
  clear: both;
  margin-right: 5px;
  font-weight: 400;
}
.am-product-options dt:after {
  display: inline-block;
  content: ':';
}
.am-product-options dt + dd {
  float: left;
}
@media only screen and (max-width: 480px) {
  .am-layout-negative-offset {
    margin-right: -25px;
    margin-left: -25px;
    padding-right: 25px;
    padding-left: 25px;
  }
}
@media only screen and (max-width: 768px) {
  .am-layout-negative-offset {
    margin-right: -35px;
    margin-left: -35px;
    padding-right: 35px;
    padding-left: 35px;
  }
}
@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
